/* Normalize */

ul {
    list-style-type: none;
    margin-before: 0;
    margin-after: 0;
    margin-start: 0px;
    margin-end: 0px;
    padding-start: 0;
}

ul,
li {
    list-style-type: none;
}

#root { /* Specific to this exercise implementation. */
    max-width: 600px;
    margin: 4em auto 0 auto;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Segmented Control

  Displayed as a table layout with evenly spaced cells and two rows,
  one for the text tabs and one for the moving bar below them.
*/

.seg-control,
.seg-tab {
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
    font-size: 16px;
    letter-spacing: 0.5px;
}

.seg-control {
    width: 100%;
    display: table;
    table-layout: fixed;
    width: 100%;
    padding: 0;
}

.seg-tabs,
.seg-line {
    display: table-row;
    width: 100%;
    padding: 0;
}

.seg-tab,
.seg-bar {
    display: inline-block;
    text-align: center;
    background: transparent;
    border: none;
    outline: none;
    color: #A7A7A7;
    display: table-cell;
    margin: 0;
    padding: 0;
}

.seg-tab:hover {
    color: #898989;
}

.seg-tab,
.seg-line {
    opacity: 0;
    -webkit-animation: fadeIn ease-out 1;
            animation: fadeIn ease-out 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
}

/* Intro animation delay */

.seg-tab:nth-child(2) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.seg-tab:nth-child(3) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.seg-tab:nth-child(4) {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

.seg-tab:nth-child(5) {
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

.seg-tab:nth-child(6) {
    -webkit-animation-delay: 1.0s;
            animation-delay: 1.0s;
}

.seg-tab:last-child {
    margin-right: 0;
}

.seg-tab input {
    position: absolute;
    opacity: 0;
}

.seg-tab input:focus + label {
    border-color: rgba(74, 144, 226, 0.8);
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(74, 144, 226, 0.6);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(74, 144, 226, 0.6);
    outline: 0 none;
}

.seg-tab label {
    display: block;
    margin: 0 -1px -1px 0;
    padding: 1em .25em;
    text-align: center;
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.segtab[aria-selected="true"],
.seg-tab.selected {
    color: #4A90E2;
}

/* Segmented Control Bar */

.seg-line {
    background-color: #D3D3D3;
    height: 2px;
}

.seg-bar.selected {
    will-change: transform;
    visibility: visible;
    background-color: #4A90E2;
    height: 4px;
    position: relative;
    top: -1px;
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
    -webkit-box-shadow: 0 2px #4A90E2;
            box-shadow: 0 2px #4A90E2;
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: -webkit-transform 0.3s ease-in-out;
    -o-transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}



